import { storeUtils, toProxy, watchUpdates } from "@fanfanlo";

export interface ISocketSettingData{
    port:number;
    ip:string;
    url:string;
}

export interface IPageData{
    socketSettingData:ISocketSettingData;
}

function createDefPageData():IPageData{

    const def:IPageData = {
        socketSettingData:{
            port:7001,
            ip:""  ,
            url:""     
         }
    }
    const res = storeUtils.getNamespaceStoreWithData('markets-dev-settings', {defData:def, forceReset:true});
    return res.data;
}

function loadPageData(){

}
let dataImpl:IPageData|undefined
export function marketsDevSettingsLoadDefData():IPageData{
    if(dataImpl){
        return dataImpl;
    }
    let data:IPageData = createDefPageData();
    dataImpl = toProxy(data);
    watchUpdates(dataImpl,()=>{
        console.log('dataImpl changed', dataImpl)
    })
    return dataImpl;
}