import { AppbarContainer, AppPageDataWrapperShell, AppShell } from "@app/ui";
import { Dispatcher, useTranslation } from "@fanfanlo"
import { MarketsDevSettingsContent } from "@src/components/page/markets-dev-settings/content/MarketsDevSettingsContent";
import { marketsDevSettingsLoadDefData } from "@src/components/page/markets-dev-settings/data/data.intf";
import { ContextWrapper } from "@fanfanlo";
import { MarketsDevSettingsContextProvider } from "@src/components/page/markets-dev-settings/data/context";
export default function MarketsDevSettingsPage() {
    const { t } = useTranslation('homepage/pages/markets-dev-setting/content');
    const data = marketsDevSettingsLoadDefData()
    const dispatcher = new Dispatcher()

    return (
        <AppPageDataWrapperShell
            contextValue={data}
            ContextProvider={MarketsDevSettingsContextProvider}
        >
            <AppbarContainer appbarProps={{ title: t("content.title"), back: true, backDispatcher: dispatcher }}>
                <MarketsDevSettingsContent />
            </AppbarContainer>
        </AppPageDataWrapperShell>
    )

    // return (
    //     <ContextWrapper
    //         contextValue={data}
    //         ContextProvider={MarketsDevSettingsContextProvider}
    //     >
    //         <AppShell>
    //             <AppbarContainer appbarProps={{ title: t("content.title"), back: true, backDispatcher: dispatcher }}>
    //                 <MarketsDevSettingsContent />
    //             </AppbarContainer>
    //         </AppShell>
    //     </ContextWrapper>
    // )
}