import { useTranslation } from "@fanfanlo";
import { Box, Stack, TextField } from "@mui/material";
import { useState } from "react";
import { useMarketsDevSettingsContext } from "../data/context";

function isValidWebSocketURL(url:string) {
    return /^wss?:\/\/[\w.-]+(:\d+)?(\/.*)?$/.test(url) || url.length == 0;
}

function URL() {
    const { t } = useTranslation('homepage/components/page/markets-dev-settings/content/content');
    const pageData = useMarketsDevSettingsContext()
    const [value, _setValue] = useState(pageData.socketSettingData.url)
    const [isWrong, setIsWrong] = useState(!isValidWebSocketURL(value))
    function setValue(v: string) {
        _setValue(v)
        if (!isValidWebSocketURL(v)) {
            setIsWrong(true)
        } else {
            setIsWrong(false)
            pageData.socketSettingData.url = v
        }
    }
    const s = t("DevSettings.socket.url.helperText", {0:"socket",1:"ws",2:"wss",3:"url"})
    return (
        <Box>
            <TextField
                error={isWrong}
                label={t("DevSettings.socket.url.name")}
                value={value}
                placeholder={"ws://192.168.1.2:7001"}
                helperText={isWrong ? s : ""}
                variant="standard"
                onChange={(e) => setValue(e.target.value)}
            />
        </Box>
    )
}
export function DevSettings() {
    return (
        <>
            <Box>
                <Stack direction={"column"} spacing={2}>
                    <URL />
                </Stack>
            </Box>
        </>
    )
}